html {
  height: 100%;
  min-height: 100%;
  background-color: #faf8ef; }

.header {
  margin-bottom: 10px;
  background-color: #8f7a66;
  height: 40px;
  border-radius: 10px;
  position: relative; }
  .header a {
    color: #f9f6f2;
    text-decoration: none;
    padding: 10px;
    position: absolute;
    margin: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%); }
  .header .home {
    border-right: 1px; }

.layout {
  display: flex;
  justify-content: center; }

.title {
  display: flex;
  justify-content: center;
  color: #8f7a66; }

.score {
  float: right;
  margin-bottom: 15px;
  background-color: #beaca6;
  color: #fef7fa;
  border-radius: 5px;
  display: inline-block;
  padding: 5px;
  text-align: center; }
  .score .label {
    display: block;
    color: #e9d9c6; }

.game {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  border-radius: 5px;
  height: 400px;
  width: 400px;
  clear: both;
  background-color: #beaca6; }
  .game .emptyTile, .game .tile2, .game .tile4, .game .tile8, .game .tile16, .game .tile32, .game .tile64, .game .tile128, .game .tile256, .game .tile512, .game .tile1024, .game .tile2048 {
    flex-grow: 1;
    flex-basis: 21%;
    margin: 5px;
    border-radius: 5px;
    border-color: blue;
    display: flex;
    align-items: center;
    justify-content: center; }
  .game .emptyTile {
    background-color: #776e65; }
  .game .tile2 .number, .game .tile4 .number, .game .tile8 .number, .game .tile16 .number, .game .tile32 .number, .game .tile64 .number, .game .tile128 .number, .game .tile256 .number, .game .tile512 .number, .game .tile1024 .number, .game .tile2048 .number {
    position: absolute;
    font-size: xx-large; }
  .game .tile2 {
    color: #786d68;
    background-color: #eee4da; }
  .game .tile4 {
    color: #786d68;
    background-color: #ece0c6; }
  .game .tile8 {
    color: #fef7fa;
    background-color: #f1b078; }
  .game .tile16 {
    color: #fef7fa;
    background-color: #ed8b51; }
  .game .tile32 {
    color: #fef7fa;
    background-color: #f47c62; }
  .game .tile64 {
    color: #fef7fa;
    background-color: #e95937; }
  .game .tile128 {
    color: #fef7fa;
    background-color: #f2d86a; }
  .game .tile256 {
    color: #fef7fa;
    background-color: #e3c02c; }
  .game .tile512 {
    color: #fef7fa;
    background-color: #e2b913; }
  .game .tile1024 {
    color: #fef7fa;
    background-color: #ecc400; }
  .game .tile2048 {
    color: #fef7fa;
    background-color: #5eda92; }
