html {
  height: 100%;
  min-height: 100%;
  background-color: #faf8ef; }

.header {
  margin-bottom: 10px;
  background-color: #8f7a66;
  height: 40px;
  border-radius: 10px;
  position: relative; }
  .header a {
    color: #f9f6f2;
    text-decoration: none;
    padding: 10px;
    position: absolute;
    margin: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%); }
  .header .home {
    border-right: 1px; }

.layout {
  display: flex;
  justify-content: center; }

.title {
  display: flex;
  justify-content: center;
  color: #8f7a66; }
